@mixin resetDivs() {
    margin : 0;
    padding: 0;
    border: 0;
    line-height: 3vh;  
}

body{
    font-family: sans-serif;  
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

div{
    width: 10%;
    height: 15vh;
    margin : 0.2% 0 0 10%;
    line-height: 15vh;
    text-align: center;
    border: 2px solid grey;
    border-radius: 0px;
    
}

.fonts{
    @include resetDivs();
    margin-top: 1%;
    margin-left: 10%;
}
.select_theme{
    position:fixed;
    width: 50px;
    height: 100px; 
    top: 20%;

    div{
        width: 25px;
        height: 25px;
        border: 1 solid black;               
    }
}

.theme_red{
    background-color: red;
}
.theme_blue{
    background-color: blue;
}